<template><div class="content"><h1 class="post-title">生存与共情<a class="header-anchor" id="%E7%94%9F%E5%AD%98%E4%B8%8E%E5%85%B1%E6%83%85" href="#%E7%94%9F%E5%AD%98%E4%B8%8E%E5%85%B1%E6%83%85">#</a></h1><div class="metabar"><div class="metabar-item">2023/11/27</div><div class="metabar-item">思想</div><div class="metabar-item">约 9 分钟读完</div></div>
<p>生存是人的一种原始需求。在客观上看来，生理上的生存，是人在这个世界上的肉体得以持续存在，从而为个体精神提供庇护的一种必要条件。除去特殊地区，人们的生理生存需求似乎早已被现代化、工业化的彻底满足，至于对这些需求的实现的选择，则完全取决于个人，这不影响其本身的确被满足这一事实。因而，对于那些可以称得上处于现代化社会中的人们，对生存的需求较多地集中在社会的生存上。然而，仍有一项特殊的生理生存值得讨论。</p>
<h2>繁衍<a class="header-anchor" id="%E7%B9%81%E8%A1%8D" href="#%E7%B9%81%E8%A1%8D">#</a></h2>
<p>许多人不是为自己而活，这是显而易见的事实。我在此并不能找到确凿的或可信的，对「究竟为自己而活能对自己和他人带来什么样的好处」、「社会是否需要只为自己而活的人」以及「只为自己而活的人对社会有何意义」等问题的回答，因此不失严谨性地，把「是否为自己而活」中是与否的选择完全当成是具有可信的逻辑性的理由的一种个人选择。</p>
<p>那么对于他们其中的部分人，以及为自己而活的人的其中的部分人而言，生存可能有着另外的意义，即不仅在于个体的持续存在，更在于个体的延续。最基本的对延续的考虑是生物层面上的本能，并无多少有意义的解释，而我个人所认为具有思维性的延续，是对个体意志乃至目标的延续。对于后者而言，生存的需求拓展到了个体之外，即对其而言的生存是血脉的生存，从而对血脉传承的威胁可能被直接转化为对他个人乃至血脉相关联的所有人的威胁。</p>
<p>所以对于一部分人来说，繁衍可以作为是生存的一个方面；繁衍受到了威胁，生存也一样受到了威胁。</p>
<h2>社会生存<a class="header-anchor" id="%E7%A4%BE%E4%BC%9A%E7%94%9F%E5%AD%98" href="#%E7%A4%BE%E4%BC%9A%E7%94%9F%E5%AD%98">#</a></h2>
<p>自从人类遍布地球以来，人类所构筑起的主要的社会已经具有了对大多数人类的普适性。不可否认，地球上至今为止仍然有脱离主社会的原始部落的存在，然而他们的生存与本文所探讨的生存的关联甚小——这是一种可以被描述为「动物性的」生存的存在。我们所处的主要社会的生产力的进步使得任何主动选择倒退其成果的特殊社会难以恰如其分地运转，但这不代表这样的社会无法运转或者存在。下文主要探讨我们所处的主要社会。</p>
<p>融入社会的目的是在社会中求得生存；生活在社会中的人需要社会。社会几乎容纳了在其中的个体存在所需要的所有因素，基础的包括生存上的保障（如文章开头所述），较为高层的包含个体精神交换的平台。</p>
<p>「融入社会」可能在其表现上类似于一种生物上的适应，却在具体的实现上处处有其特殊性。最基本的典型例子可能包括个体精神需求的改变（为了迎合社会的需求）以至于个体生理需求的改变。在这里人做出变化的顺序似乎按照的是从高到低的顺序，这是因为越底层的需求与个体最基本的生理生存的关联越紧密，从而对于个体而言做出改变所要付出的代价越多。然而，也有不那么典型的例子中，个体对社会的某些层面进行突破，从而让社会以及其中的一部分人发生了妥协。显然，这种突破所需要的力量是无比巨大的，因为它牵涉到了其他个体需求的不满足，乃至于冲击——社会是一个需要平衡才能正常运转的整体，它其中的个体，或小整体，共同决定了这一平衡维持的条件。</p>
